.container {
  background-color: transparent;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
  transform-style: preserve-3d;
  transform: perspective(1500px);
  transition: transform ease 1s;
  width: 1000px;
}

.holder {
  box-shadow: 0px 16px 40px rgba(30, 30, 30, 0.2);
  /* border-radius: 10px; */
  max-width: 800px;
}

@media only screen and (max-width: 850px) {
  .container {
    width: 100%;
    transform: none;
  }

  .holder {
    padding: 0px 0px 15px 0px;
    margin: 0px;
    box-shadow: none;
    transform: none;
  }
}
